  .glx-navigator-horizontal--right{
    right: 0;
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
  }
}
.glx-navigator-horizontal{
  @extend .glx-flex-row,.glx-flex-start,.glx-flex-g-4,.glx-m-4,.glx-pt-8,.glx-w-full-4;
  overflow-y: scroll;

}

.glx-badge {
  @extend .glx-p-6,.glx-flex-g-4,.glx-no-select;
  display: flex;
  white-space: nowrap;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  background: $main-contrast;
  button{
    border: none;
    background-color: transparent;
  }
}


.glx-badge--red{
  background: $main-red;
  color: $main-white;
}


.glx-badge--black{
  background: $main-black;
  color: $main-white;
}


.glx-badge--green{
  background: $main-green;
}
.glx-badge--orange{
  background: $main-orange;
  color: $main-black;
  button{
    color: $main-black;
  }
}
.glx-badge--yellow{
  background: $main-yellow;
  color: $main-black;
  button{
    color: $main-black;
  }
}


.glx-persona {
  @extend .glx-no-select;
  display: flex;
  justify-content: center;

  .glx-persona--center {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4em;
    border-radius: 100px;
    background: $main-contrast;
    color: $main-text !important;
    height: 200px;
    width: 200px;
  }

  .glx-persona--small {
    font-size: 1em;
  }

  img{
    width: 100%;
    height: 100%;
    border-radius: 100px;
  }
}


.glx-tag-selector{
  position: relative;
  min-width: 200px;

  .glx-tag-selector--drawer{
    @extend .glx-p-8,.glx-w-full-8;
    z-index: 88;
    position: absolute;
    background-color: $main-accent-01;
    >div{
      @extend .glx-no-select;
      width: 100%;
    }
    >div:hover{
      @extend .glx-no-select;
      background-color: $main-accent-05;
      width: 100%;
    }

  }
}


.glx-user-block{
  @extend .glx-flex-r;
  position: relative;
  .glx-user-block--drawer{
    z-index: 88;
    width: calc(100% - 32px);
    position: absolute;
    top: 0;
    transform: translateY(32px);

    >div{
       @extend .glx-no-select,.glx-pointer;
      padding: 4px;

    }
    >div:hover{
      background-color: $main-accent-04 !important;
    }
    >div:nth-child(even){
      background-color: $main-accent-03;
    }
    >div:nth-child(odd){
      background-color: $main-accent-01;
    }
  }
}
